import React from 'react'

import ticket from '../img/ticket.jpg'

function Ticket() {
  return (
    <div className='ticket-container'>
        <div className='ticket-header'>
            <div className='ticket-img'>
                <img src={ticket} alt='ticket'></img>
            </div>
            <div className='ticket-info'>
                <h1>MUSEUM , MOVIE , ATRACTION ?</h1>
                <h2>ALL FOR YOU</h2>
                Oto cztery punkty dotyczce sprzeday biletw do muzeum, atrakcji, kina i zabaw:

<p>1. **Rnorodno ofert**: Bilety do muzew, parkw rozrywki i kin czsto s dostpne w rnych wariantach, takich jak bilety standardowe, rodzinne czy grupowe. To pozwala klientom na wybr najbardziej odpowiedniej opcji, dostosowanej do ich potrzeb i liczby osb.</p>

<p>2. **Zakupy online**: Coraz wicej instytucji kultury i rozrywki oferuje moliwo zakupu biletw online, co znacznie uatwia proces rezerwacji. Klienci mog unikn kolejek, a dziki dostpnym znikom i promocjom czsto zaoszczdzi na zakupach.</p>

<p>3. **Promocje i rabaty**: Wielu operatorw atrakcji organizuje rnorodne promocje, takie jak dni tematyczne, zniki dla studentw, seniorw oraz oferty specjalne w okrelonych dniach tygodnia. Dziki temu zwiedzanie muzew, kino czy korzystanie z atrakcji staje si bardziej dostpne dla szerokiego grona odbiorcw.</p>

<p>4. **Wydarzenia specjalne**: Bilety na wydarzenia specjalne, takie jak wystawy czasowe w muzeach, premiery filmowe czy organizowane festyny, przycigaj dodatkowych goci. Przedsibiorstwa czsto promuj te wydarzenia poprzez kampanie marketingowe, co zwiksza zainteresowanie i uczestnictwo w danej atrakcji.</p>

            </div>
        </div>

    </div>
  )
}

export default Ticket